header {
    height: 100vh;
    overflow: hidden;
    padding: 7rem;
    background-size: 2000px 1000px;
    background-image: url(../../assets/background-1.png);
    
  }
  
  .name {
    background: #ff2a11;
    background: linear-gradient(to right, #ff2a11 9%, #ff9c38 53%, #bdad19 79%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .header__container {
    text-align: center;
    height: 100%;
    position: relative;
  }
  
  .ctme {
    margin-top: 2rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
  }
  
  .cool {
    background: linear-gradient(
      var(--color-primary-variant),
      var(--color-primary)
    );
    width: 25rem;
    height: 24rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 3rem;
    border-radius: 8rem 8rem 0 0;
    padding:5rem 1.5rem 1.5rem 1.5rem;
    overflow:hidden
  }
  
  
  @media screen and (max-width:1024px) {  
    header{
      height: 68vh
    }
  }
  @media screen and (max-width:600px) {
    header{
      height:100vh;
    }
    
  }